___CSS_LOADER_EXPORT___.push([module.id, `.ny-collision-dashboard {
  font-family: Arial, sans-serif;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px; }
  .ny-collision-dashboard h1 {
    text-align: center; }

.filter-wrap {
  margin-bottom: 20px; }
  .filter-wrap label {
    font-weight: bold; }

.charts-wrap {
  display: flex;
  justify-content: space-between; }

.chart {
  width: 48%;
  height: auto;
  border-radius: 5px;
  padding: 20px;
  background-color: #f7f7f7;
  box-sizing: border-box; }

@media (max-width: 767px) {
  .charts-wrap {
    flex-direction: column; }
  .chart {
    width: 100%;
    margin-bottom: 20px; } }

.tooltip {
  position: absolute;
  background: white;
  border: 1px solid #000;
  padding: 5px;
  font-size: 12px; }

.hide {
  visibility: hidden; }
